.start-modal {
  background-color: darkcyan;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
}
.start-modal h1 {
  text-align: center;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  text-transform: uppercase;
}
.form-page {
  width: 350px;
  height: 350px;
  text-align: center;
  border: 1px solid black;
  background-color: cadetblue;
  margin: 50px auto;
  position: relative;
}

.form-page input {
  width: 150px;
  height: 35px;
  padding: 5px;
  margin: 10px 0;
  outline: 0;
  font-size: 30px;
  font-family: "Bungee Spice", cursive;
  caret-color: orange;
}
.btns {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.btns:hover {
  background-color: dodgerblue;
  color: white;
}
.erorr {
  color: red;
  text-align: center;
  font-size: 30px;
}
